@import "../../../../../common";

.rnd {
  z-index: $dialog-z-index;

  .Dialog {
    .windowBox {
      display: flex;
    }

    .inputs {
      display: flex;
      flex-direction: column;

      > * {
        margin-bottom: $margin * 2;
      }

      :first-child {
        margin-top: $margin;
      }

      input, textarea {
        padding: $padding;
        border-radius: $border-radius;
      }

      .buttons {
        position: sticky;
        bottom: 0;
      }
    }
  }
}
